<!--第三版banner，设计为通用banner组件，可以简单的形成后台可配置的轮播图，同时为开发者提供一些必要的选择-->
<template>
    <div class='swiper-main'>
        <div v-if='status == "error"' class="banner-img">
            <img @load=staticBannerLoad(1) :class='{visible:staticVisible}' width=100% height=100% src="../../page/home/img/banner1.jpg">
        </div>
        <swiper v-else-if='status == "success"' ref="mySwiper" :options='swiperOption'>
            <template v-for='(item, index) in bannerList'>
                <swiper-slide :key=index>
                    <div class="banner-img">
                        <img @load=bannerLoad(index) :class='{visible:item.visible}' width=100% height=100% :src="item.img_url">
                    </div>
                </swiper-slide>
            </template>
        </swiper>
        <div class="loader" v-else>
            <div class="loader-inner ball-spin-fade-loader">
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
        </div>
    </div>
</template>

<script>
    import {swiper, swiperSlide} from 'vue-awesome-swiper'
    import axios from 'axios';
    import urlConfig from '@/config'

    export default{
        components:{
            swiper,
            swiperSlide,
        },
        data () {
            return {
                swiperOption: {
                    //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
                    preventClicks: false,
                    autoplay: {
                        delay: 3000,
                        stopOnLastSlide: false,
                        disableOnInteraction: false
                    },
                    direction: 'horizontal',
                    freeMode:false,
                    loop: true,
                    observer: true,
                    observeParents: true,
                    // autoplayDisableOnInteraction: false,
                    // onlyExternal: false,//v3.0写法
                    // allowTouchMove: true//v4.0写法
                },
                bannerList:[],
                status:'',
                calltime:5,
                staticVisible:false,
            }
        },
        props:{
        },
        computed: {
            swiper() {
              return this.$refs.mySwiper.swiper;
            }
        },
        mounted(){
            let getBanner = () => {
                this.getBannerData()
                    .then((data) => {
                        try{
                            let list = data.data.data.data.list;
                            for(let i = 0;i<list.length;i++){
                                list[i].visible = false;
                                this.bannerList.push(list[i]);
                            }
                            if(list&&list.length > 0)
                                this.status = 'success';
                            else this.status = 'error';
                        }catch(e){
                            console.log(e);
                            this.status = 'error';
                        }
                    })
                    .catch((e) =>{
                        this.calltime--;
                        if(this.calltime == 0){
                            this.status = 'error';
                            return false;
                        }
                        getBanner();
                    });
            };
            getBanner();
        },
        methods:{
            getBannerData(){
                let param = JSON.stringify({group_id:urlConfig.bannerGroupID});
                let apiurl = `/api/zixun/get-banner?condition=${param}`;
                // console.log(apiurl)
                return axios.get(apiurl,{
                    timeout: 5000,
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
                        // 'Accept-Ranges': 'bytes',
                        // 'Access-Control-Allow-Credentials': true,
                        // 'Access-Control-Allow-Origin': '*'
                    }
                })
            },
            staticBannerLoad(){
                this.staticVisible = true;
            },
            bannerLoad(index){
                this.bannerList[index].visible = true;
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../common/css/loaders/animations/ball-spin-fade-loader.scss';
    .loader{
        width:100%;
        height:100%;
        .ball-spin-fade-loader{
            position:relative;
            left:50%;
            top:2.2rem;
            div{
                background-color:#ff5455;
            }
        }
    }
  @import './swiper.min.css';

  @function px2rem ($px) {
    @return ($px / 75) * 1rem
  };
  .swiper-slide-duplicate{
      img{
          opacity:1 !important;
      }
  }
  .swiper-main{
    height:100%;
    // div{
    //   height: 100%;
    // }
    img{
        opacity:0;
        transition:opacity 0.3s ease-in;
        &.visible{
            opacity:1;
        }
    }
    .activity-rule {
      position: absolute;
      text-align: center;
      background: rgba(0, 0, 0, .5);
      color: #fff;
      padding: 0 px2rem(16);
      /*width: px2rem(166);*/
      height: px2rem(45);
      border-radius: px2rem(45);
      line-height: px2rem(45);
      right: px2rem(30);
      top: px2rem(30);
      z-index: 10;
      .arrow {
        display: inline-block;
        vertical-align: baseline;
        width: px2rem(20);
        height: px2rem(20);
        line-height: px2rem(20);
        text-align: center;
        border: 1px solid #fff;
        border-radius: 50%;
      }
    }
    img {
        width: 100%;
        vertical-align: bottom;
    }

  }

  .activity-dialog {
    .header {
      font-size: px2rem(32);
      margin-bottom: px2rem(10);
    }
    .grey-desc {
      overflow: scroll;
      height: px2rem(480);
      font-size: px2rem(26);
      line-height: px2rem(40);
      color: #666;
      padding: px2rem(30) px2rem(10) px2rem(30) px2rem(50);
      background: #f0f0f0;
      text-align: left;
      span {
        margin-left: px2rem(-30);
      }
    }
    .footer {
      width: px2rem(260);
      height: px2rem(70);
      line-height: px2rem(70);
      background: #ea4949;
      border-radius: px2rem(35);
      margin-top: px2rem(20);
      border: none;
      outline: none;
      span {
        font-size: px2rem(24);
        color: #fff;
      }
    }
  }
</style>



