<template>
  <form class='lesson-form' ref="lessonForm" :style="formStyle">
    <div class="form-content">
      <h2 v-show="showTitle" v-html="TitleText"></h2>

      <div class="form-item">
        <input type="text" placeholder="请输入孩子姓名" class="username" :class="{alert : userNameAlert}" v-model="userName">
      </div>

      <div class="form-item flex-item">
        <select class="grade" v-model="weakGrade" @change="handleGradeChange" ref="grade">
          <option :value="item[0]" v-for="item in subjectGrade">
            {{item[1]}}
          </option>
        </select>

        <select class="subject" v-model="weakSubject" ref="subject">
          <option :value="item[0]" v-for="item in weakSubjectArr">
            {{item[1]}}
          </option>
        </select>
      </div>

      <div class="form-item">
        <input type="text" :placeholder="placeholderTel" :class="{alert : mobileAlert}" class="tel" v-model="mobile">
      </div>

      <div class="form-item vertify-code" v-show="vertifyCodeStatus">
        <input type="text" placeholder="验证码" class="identifyingcode" v-model="identifyingCode">
        <span class='get-identify-code' @click.stop="identifySubmitBtn">{{codeCountDown}}</span>
      </div>
    </div>

    <div :style="btnStyle" class="zm-btn" @click="handleSubmitBtn">
      <span>{{btnText}}</span>
    </div>

    <div class="info" v-if="showInfo">
      <img src="./lock@2x.png">
      <span>为了您的权益，您的隐私将被严格保密</span>
    </div>
    <success-dialog ref="successDialog"></success-dialog>
    <fail-dialog ref="failDialog"></fail-dialog>
  </form>
</template>

<script>
  import api from '../../api'
  import {request, queryObj, isEmptyObject} from '@/common/js/utils'

  import failDialog from '@/component/fail-dialog/fail-dialog'
  import successDialog from '@/component/success-dialog/success-dialog'
  import Toast from '@/base/toast/toast'

  import {mapState} from 'vuex'

  export default {
    name: 'lesson-form',
    data() {
      return {
        getCode: '获取验证码',
        codeCountDownNum: 60,
        codeCountSubmitNum: 3,
        vertifyCodeStatus: false,
        vertifyOnOff: true,
        submitOnOff: true,
        userName: '',
        weakGrade: '幼儿园中班',
        weakSubject: '数理逻辑训练',
        gradeData: [],
        weakSubjectArr: [],
        mobile: '',
        identifyingCode: '',
        url: '',
        channelProgram: '',
        // showGrade: ['小二','小三','小四','小五','小六','初一','初二','初三','初四','高一','高二','高三'],
        userNameAlert: false,
        mobileAlert: false,
        success: false
      }
    },
    watch: {
      subjectData: function () {
        // 异步获取到subjectData中的数据的话就执行这里
        // 默认显示高一
        this.$refs.grade.value = '幼儿园中班';
        this.$nextTick(() => {
          this.handleGradeChange();
          this.autoCompleteForm();
        })
      },
      userName() {
        this.userNameAlert = false;
      },
      mobile() {
        this.mobileAlert = false;
      }
    },
    mounted () {
      console.log("1")
      // subjectData中的数据已经获取到的话就执行这里
      if (this.subjectData.length) {
        // 默认显示幼儿园中班
        this.$refs.grade.value = '幼儿园中班';
        this.handleGradeChange();
        this.autoCompleteForm();
      }
    },
    updated() {
      // this.handleGradeChange();
    },
    components: {
      Toast,
      successDialog,
      failDialog
    },
    props: {
      placeholderTel: {
        type: String,
        default: '请输入手机号'
      },
      cssType: {
        type: String,
        default: 'default'
      },
      showTitle: {
        type: Boolean,
        default: true,
      },
      TitleText: {
        type: String,
        default: '免费领取<span style="color: #F65051; font-size: 0.733rem; padding: 0 0.1rem;">300元</span>试听课'
      },
      btnStyle: {
        type: Object,
      },
      btnText: {
        type: String,
        default: '0元试听',
      },
      wxLinkUrl: {
        type: String,
        default: '',
      },
      formStyle: {
        type: Object,
        default() {
          return {}
        }
      },
      showInfo: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      ...mapState(['subjectData', 'subjectGrade', 'subjectToLabel']),
      codeCountDown() {
        if (this.codeCountDownNum == 60 || this.codeCountDownNum <= 0) {
          return this.getCode

        } else {
          return this.codeCountDownNum + 's'
        }
      },
      submitCountDown() {
        return this.submitOnOff
      },
    },
    created() {
      this.$store.dispatch('getSubjectData');
      this.changeStatusByPlatform();
    },
    methods: {
      //alert(this.props.successType);
      //带URL参数自动填入表单
      autoCompleteForm() {
        const query = queryObj();
        if (!isEmptyObject(query)) {
          for (let key in query) {
            if (query['userName'])
              this.userName = decodeURIComponent(query['userName']);
            if (query['grade'])
              this.weakGrade = decodeURIComponent(query['grade']);
            //            if (this.weakGrade == '初四') {
            //              this.weakGrade = '初三';
            //            }
            this.$refs.grade.value = this.weakGrade;
            if (query['weakSubject'])
              this.weakSubject = decodeURIComponent(query['weakSubject'])
            if (query['mobile'])
              this.mobile = decodeURIComponent(query['mobile'])
            if (query['channelProgram'])
              this.channelProgram = decodeURIComponent(query['channelProgram'])
          }

          //对应的年级，自动切换科目

          var $this = this.$refs.grade;

          if ($this.selectedIndex >= 0) {
            switch ($this.selectedIndex) {
              case 0:
                this.weakSubjectArr = this.subjectData[0]
                this.weakSubject = this.subjectData[0][0][0]
                $this.options[$this.selectedIndex].selected = true;
                break
              default:
                this.weakSubjectArr = this.subjectData[$this.selectedIndex]
                var tmpArr1 = [];
                var tmpArr2 = [];
                this.weakSubjectArr.map((item, index) => {
                  tmpArr1.push(item[0])
                  tmpArr2.push(item[1])
                })
                //                console.log(test.includes("111"))
                //                console.log(this.weakSubjectArr)
                //                if (JSON.stringify(this.weakSubjectArr).indexOf(this.weakSubject) != -1) {
                //                console.log(tmpArr1)
                //                console.log(tmpArr2)
                if (tmpArr1.includes(this.weakSubject) || tmpArr2.includes(this.weakSubject)) {
                  if (this.subjectToLabel[this.weakSubject]) {
                    this.weakSubject = this.subjectToLabel[this.weakSubject]
                  }
                } else {
                  this.weakSubject = this.subjectData[$this.selectedIndex][0][0]
                }
                break
            }
          } else {
            if (JSON.stringify(this.subjectGrade).indexOf(this.weakGrade) != -1) {
            } else {
              this.weakGrade = "幼儿园中班"
            }
            this.weakSubjectArr = this.subjectData[0]
            this.weakSubject = this.subjectData[0][0][0]
          }
        }
        //this.handleGradeChange();
      },
      changeStatusByPlatform() {
        const urlParams = queryObj() || {}
        if (request('cooperatorStore') != '') {
          this.vertifyCodeStatus = true
        }
        api.getPlatformOnOffStatus(urlParams).then((res) => {
          if (res.data.code === 0) {
            if (request('cooperatorStore') == '' && res.data.data == false) {
              this.vertifyCodeStatus = false
            } else {
              this.vertifyCodeStatus = true
            }
          } else {
            // console.log(res.data.msg)
          }
        }).catch(err => {
          console.log(err)
        })
      },
      handleSubmitBtn() {
        console.log(this.wxLinkUrl)
        let self = this
        if (this.vertifyCodeStatus) {
          this.url = 'api/student/register-and-book-by-valid-code-firststep'
        } else {
          this.url = 'api/student/register-and-book'
        }
        this.validateForm().then(() => {
          if (self.submitOnOff) {
            const timer2 = setInterval(() => {
              this.codeCountSubmitNum--
              if (this.codeCountSubmitNum <= 0) {
                clearInterval(timer2)
                this.codeCountSubmitNum = 3
                self.submitOnOff = true
              } else {
                this.submitOnOff = false
              }
              return this.submitOnOff
            }, 1000)

            // console.log(this.$refs.grade)
            const parmas1 = {
              name: self.userName,
              grade: self.weakGrade,
              weakSubject: self.weakSubject,
              mobile: self.mobile,
              identifyingCode: self.vertifyCodeStatus === true ? self.identifyingCode : '',
              completed: '1',
              channelProgram: self.channelProgram,
              channel_code: self.$route.query.channel_code || '',
              channel_keyword: self.$route.query.channel_keyword || ''
            }
            const parmas2 = {
              name: self.userName,
              grade: self.weakGrade,
              weakSubject: self.weakSubject,
              mobile: self.mobile,
              completed: '0',
              channelProgram: self.channelProgram,
              channel_code: self.$route.query.channel_code || '',
              channel_keyword: self.$route.query.channel_keyword || ''
            }

            var parmas3 = {
              referMobile: self.$route.query.referMobile || '',
              stuGrade: self.weakGrade || '',
              stuMobile: self.mobile || '',
              stuName: self.userName || '',
              weakSubject: self.weakSubject || ''
            }

            if (this.vertifyCodeStatus) {
              this.parmas = parmas1
            } else {
              this.parmas = parmas2
            }
            const urlParams = queryObj()

            // for (let key in urlParams) {
            //   this.parmas[key] = urlParams[key]
            // }
            if (this.parmas.grade == "初四") {
              this.parmas.grade = "初三"
            }

            if (this.$route.name == "youke-home") {
              this.parmas.comeFrom = "优课m站"
              if (self.$route.query.province != null || self.$route.query.province != '') {
                this.parmas.province = self.$route.query.province
              }

              if (self.$route.query.city != null || self.$route.query.city != '') {
                this.parmas.city = self.$route.query.city
              }

              if (self.$route.query.area != null || self.$route.query.area != '') {
                this.parmas.area = self.$route.query.area
              }
              this.parmas.channelProgram = "m站"
            }

            if (self.$route.query.referMobile) {
              console.log("转介绍")
              api.appointmentForMpReferral(parmas3).then(res => {
                console.log(res)
                if (res.data.code === 0) {
                  if (res.data.data.data) {
                    if (self.weakGrade == '小六' || self.weakGrade == '小五' || self.weakGrade == '小四' || self.weakGrade == '小三' || self.weakGrade == '小二' || self.weakGrade == '小一' || self.weakGrade == '学前班' || self.weakGrade == '幼儿园大班' || self.weakGrade == '幼儿园中班') {
                      if (self.weakSubject != '钢琴陪练') {
                        api.postLessonHasPeilian({mobile: this.mobile})
                          .then(res => {
                            if (!res.data.data.data.result) {
                              this.$router.push({
                                name: 'appointSuccess',
                                query: {
                                  mobile: self.mobile,
                                  channel_code: self.$route.query.channel_code,
                                  channel_keyword: self.$route.query.channel_keyword
                                }
                              })
                            } else {
                              this.$refs.successDialog.postFormSuccess();
                            }
                          })
                      } else {
                        this.$refs.successDialog.postFormSuccess();
                      }
                    } else {
                      this.$refs.successDialog.postFormSuccess();
                    }
                  } else {
                    res.data.msg ? Toast(res.data.data.msg || "预约失败") : this.$refs.failDialog.postFormFail();
                  }
                } else {
                  res.data.msg ? Toast(res.data.data.msg || "预约失败") : this.$refs.failDialog.postFormFail();
                }
              })
            }else {
              api.postLessonForm(this.url, this.parmas).then(res => {
                if (res.data.code === 0) {
                  if (self.weakGrade == '小六' || self.weakGrade == '小五' || self.weakGrade == '小四' || self.weakGrade == '小三' || self.weakGrade == '小二' || self.weakGrade == '小一' || self.weakGrade == '学前班' || self.weakGrade == '幼儿园大班' || self.weakGrade == '幼儿园中班') {
                    if (self.weakSubject != '钢琴陪练') {
                      api.postLessonHasPeilian({mobile: this.mobile})
                        .then(res => {
                          if (!res.data.data.data.result) {
                            this.$router.push({
                              name: 'appointSuccess',
                              query: {
                                mobile: self.mobile,
                                channel_code: self.$route.query.channel_code,
                                channel_keyword: self.$route.query.channel_keyword
                              }
                            })
                          } else {
                            this.$refs.successDialog.postFormSuccess();
                          }
                        })
                    } else {
                      this.$refs.successDialog.postFormSuccess();
                    }
                  } else {
                    this.$refs.successDialog.postFormSuccess();
                  }
                } else {
                  res.data.msg ? Toast(res.data.msg) : this.$refs.failDialog.postFormFail();
                }
              }).catch(err => {
                this.$refs.failDialog.postFormFail();
              })
            }
            this.submitOnOff = false;
            // console.log(this.$route.query);
          }
        }).catch(err => {
          Toast(err);
        })
      },
      validateForm() {
        return new Promise((resolve, reject) => {
          if (!(/^([a-zA-Z]|[\u4e00-\u9fa5]){1,}$/.test(this.userName))) {
            this.userNameAlert = true;
            reject('请填写正确的孩子姓名');
          } else if (this.userName.length >= 7) {
            this.userNameAlert = true;
            reject('请填写正确的孩子姓名');
          } else if (this.mobile.length == 0) {
            this.mobileAlert = true;
            reject('请输入手机号');
          } else if (!(/^\d{11}$/.test(this.mobile))) {
            this.mobileAlert = true;
            reject('请填写正确手机号');
          } else if (this.vertifyCodeStatus && !/^\d+$/.test(this.identifyingCode)) {
            reject('请输入正确的验证码');
          } else {
            resolve('success');
          }
        })
      },
      handleGradeChange() {
        var $this = this.$refs.grade;
        switch ($this.selectedIndex) {
          case 12:
            this.weakSubjectArr = this.subjectData[12]
            this.weakSubject = this.subjectData[12][0][0]
            $this.options[$this.selectedIndex].selected = true;
            break
          default:
            this.weakSubjectArr = this.subjectData[$this.selectedIndex]
            this.weakSubject = this.subjectData[$this.selectedIndex][0][0]
            break
        }
      },
      identifySubmitBtn() {
        if (!(/^[\u4e00-\u9fa5]{1,}$/).test(this.userName)) {
          return Toast('请填写中文姓名')
        } else if (!(/^\d+$/).test(this.mobile)) {
          return Toast('请输入手机号')
        }
        if (this.vertifyOnOff) {
          const verifyParams = {
            mobile: this.mobile,
            name: this.userName,
            grade: this.grade,
            weakSubject: this.weakSubject,
          }
          const verifyUrlParams = queryObj()

          api.sendVerifier(Object.assign(verifyParams, verifyUrlParams)).then((res) => {
            if (res.data.code === 0) {
              Toast('验证码发送成功')
              const timer = setInterval(() => {
                this.codeCountDownNum--
                if (this.codeCountDownNum <= 0) {
                  clearInterval(timer)
                  this.vertifyOnOff = true
                  this.codeCountDownNum = 60
                }
              }, 1000)
            } else {
              this.vertifyOnOff = true
              Toast(res.data.msg)
            }
          }).catch(err => {
            Toast(err)
          })
          this.vertifyOnOff = false
        }

      },
    },
  }
</script>

<style lang="scss" rel="stylesheet/less" scoped>
  @import "../../common/css/mixin.scss";
  @import "./css/lessonForm1.scss";
</style>



