<template>
  <transition name="fade">
    <div class="toast" :class="position" v-show="visible">
      <i>{{message}}</i>
    </div>
  </transition>
</template>

<script>
  export default {
    name: 'toast',
    data () {
      return {
        visible: false,
        message: '',
        position: ''
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../common/css/mixin.scss";
  .toast {
    position: fixed;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    word-wrap: break-word;
    padding: px2rem(12);
    text-align: center;
    z-index: 9999;
    font-size: px2rem(24);
    max-width: 80%;
    color: #fff;
    border-radius: px2rem(6);
    background: rgba(0, 0, 0, 0.7);
    overflow: hidden;
    i {
      font-style: normal;
    }
    &.middle {
      top: 50%;
    }
    &.bottom {
      top: 90%;
    }
    &.top {
      top: 10%;
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: transform .4s
  }

  .fade-enter, .fade-leave-active {
    transform: translate(-50%, -50%) scale(0);
  }
</style>



