<template>
  <div class="success-dialog" v-if="show">
    <div class="success-dialog-main">
      <div class="success-btn" @click="hide">
        <span>我知道啦</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'success-dialog',
  data() {
    return {
      show: false
    }
  },
  methods: {
    postFormSuccess() {
      this.show = true;
    },
    hide() {
      this.show = false;
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/less" scoped>
@import "../../common/css/mixin.scss";

.success-dialog {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
  .success-dialog-main {
    position: relative;
    width: px2rem(528);
    height: px2rem(610);
    top: 50%;
    left: 50%;
    margin-left: px2rem(-264);
    margin-top: px2rem(-305);
    background: url("./img/pic_succeed.png") no-repeat center;
    background-size: contain;
    position: relative;
    .success-btn {
      width: px2rem(334);
      height: px2rem(91);
      background: url("./img/btn_know.png") no-repeat center;
      background-size: contain;
      text-align: center;
      line-height: px2rem(91);
      position: absolute;
      bottom: px2rem(-38);
      left: px2rem(100);
      span {
        font-size: px2rem(38);
        color: #ffffff;
        letter-spacing: 0;
      }
    }
  }
}
</style>



