        <div class="detail">
          <span class="info-text">实验说明</span>
          <p v-if="testPage ==1" class="test-info">N喷所含的NaPCA玻尿酸小分子其分子结构拥有四个触角可以帮助肌肤抓取空气中的水分子，其锁水性更强，长效保湿。</p>
          <p v-if="testPage ==2" class="test-info">
            人的皮脂膜为弱酸性，而日常使用的洁面皂等均因含有NaOH而呈碱性，NaOH具有很强的清洁力，但长期使用却会破坏皮脂膜损害肌肤。实验材料：洁肤霸 他牌洁面皂 PH试纸</p>
          <p v-if="testPage ==3" class="test-info">皮肤氧化就等于老化，护肤就是一个抗氧化的过程，180洗面奶所含的高浓度精纯VC可以直击氧化根源，使你的肌肤焕然一新。</p>
          <p v-if="testPage ==4" class="test-info">大量护肤品选择添加低成本的矿物油，矿物油虽然具有很好的封闭性可以保湿但其不仅无法被皮肤吸收还会覆盖在皮肤的表面阻塞毛孔。</p>
          <p v-if="testPage ==5" class="test-info">造型喷雾很多都含有大量的酒精，如果酒精长期作用于头皮，势必引起诸多头皮问题，并对身体产生不利的影响。</p>
