          //第一个解锁
          if (this.selectAll.indexOf(13) != -1 && this.selectAll.indexOf(6) != -1 && this.selectAll.indexOf(5) != -1) {
            this.currentVideo = this.videos[0].uri
            this.hasVideo = true
          }
        } else if (this.testPage == 2) {
          //第二个解锁
          if (this.selectAll.indexOf(4) && this.selectAll.indexOf(9) && this.selectAll.indexOf(12)) {
            this.currentVideo = this.videos[1].uri
            this.hasVideo = true
