      fetchuser(){
        console.log()
        api.fetchLogicData()
          .then(res => {
            console.log(res)
            if (res.data.code == 1) {
              var value = this.$refs.input.value
              if (!value || value == '') {
                alert("请输入提示文本")
                return
              }
              var data = res.data.data
              var keyArr = data[0]
              var imgArr = data[1]
              keyArr.map((item, index) => {
                item.map((itm, idx) => {
                  if (value == itm) {
                    this.imgUrl = imgArr[index].uri
                    this.currentPage = 3
                  }
                })
              })
            } else {
              alert(res.data.msg || "出错拉")
            }
          })
      },
