        api.fetchData()
          .then(res => {
            console.log(res)
            if (res.data.code == 1) {
              var data = res.data.data
              var number = Math.floor(Math.random() * data.length)
              this.imgUrl = data[number].uri
              console.log(number)
              this.currentPage = 3
            } else {
